import "./index.scss"
import Head  from "~/components/Head";
import {axios} from "&";
import { Link } from "react-router-dom";
import {history} from "&/history"

const main = "";

export default class Dushhi extends Component{
    constructor(){
        super();
        this.state = {
            main :[]
        }
    }


    
    componentDidMount(){
        axios.get("/react/getBooks",{params:{
            skip:670,
            limit:50
        }})
        .then(res=>{
            this.setState({
                main:res.data.result
            })
            console.log(main)
        })  
    }

  
    render(){
        const {
           
        }=this.state;

        return(
            <div>
                <Head title="都市" show={true}></Head>
                
                    {
                        this.state.main.map((b,i)=>{
                            return(
                                <div key={i} className="books"  onClick={this.gotobook}>
                                    <Link to={"/book/"+b._id}  >
                                        
                                        <div className="book_img"  style={{width:"1.68rem" , height:"2.24rem" }}>
                                             <img  style={{width:"1.68rem" , height:"2.24rem" }} src={b.cover} alt=""/>
                                        </div>

                                        <div style={{width:"4.86rem", height:"2.25rem"}} className="book_txt">
                                            <p style={{ fontSize:"0.32rem" ,color:"#333" ,height:"0.4rem" ,lineHeight:"0.4rem" }} > {b.title}</p>
                                            <p style={{ fontSize:"0.25rem" ,color:"#999" ,height:"0.4rem" ,lineHeight:"0.4rem"  }} >
                                                {b.author}
                                            </p>
                                            <p style={{ width:"4.86rem",height:"0.72rem", fontSize:"0.25rem" ,color:"#999" , overflow:"hidden" ,lineHeight:"0.38rem"  }}>
                                                {b.desc}
                                            </p>
                                            <p style={{height:"0.6rem" , lineHeight:"0.6rem" , color:"#666"}}>
                                                <span>{b.status==1?"完结":"连载"} ,</span>
                                                <span>{b.tags}</span>
                                            </p>
                                        
                                        </div>
                                        
                                    </Link>
                                </div>
                            )
                        })
                    }
                
            </div>
        )
    }
}